	import uniPopup from "@/components/uni-popup/uni-popup.vue"
    export default {
        data() {
            return {
				maskclose:false,
				couponmask:false,
                title: 'product-list',
                productList: [],
                myproductList: [],
                userproductList: [],
                renderImage: false,
				allmoney:0,
				timerId: null,
				reqTime:3600,
				types:{
					type: String,
					default: ''
				},
				color:{
					type: String,
					default: '#878ea3'
				},
				list: [
					{id:1,title:"日常用品立减10元",termStart:"2019-04-01",termEnd:"2019-05-30",ticket:"10",criteria:"满50使用",state:0},
					]
            };
        },
		components: {uniPopup},
        methods: {
			//获取我的优惠券
			async mycouponlist(){
				let getopenid=uni.getStorageSync('openid');
				let info= await this.$apis.mycouponlist({openId:getopenid});
				this.list=info.data.couponList;
			},
			docheck(id){
				console.log(id,'22')
				this.list[id].state=!this.list[id].state;
				for(var i=0;i<this.list.length;i++){
					if(i!=id){
							this.list[i].state=0;
					}
				}
			},
			postcoupon(){
				let getcoupon=[];
				for(var i=0;i<this.list.length;i++){
					if(this.list[i].state){
							getcoupon.push(this.list[i])
					}
				}
				if(getcoupon.length==0){
					uni.showModal({
					    title: '温馨提示',
					    content: '确定不使用任何优惠卡券吗？',
						confirmText:'不稀罕',
					    success: function (res) {
					        if (res.confirm) {
					           console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}else{
					console.log(getcoupon,'选中的优惠券')
				}
				
			},
            loadData(action = 'add') {
                const data = [{
                        image: '../../static/img/goods/p1.jpg',
                        title: 'Apple iPhone X 256GB 深空灰色 移动联通电信4G手机',
                        originalPrice: 9999,
                        favourPrice: 8888,
                        tip: '自营'
                    },
                    {
                        image: '../../static/img/goods/p2.jpg',
                        title: 'Apple iPad 平板电脑 2018年新款9.7英寸',
                        originalPrice: 3499,
                        favourPrice: 3399,
                        tip: '优惠'
                    },
                    {
                        image: '../../static/img/goods/p3.jpg',
                        title: 'Apple MacBook Pro 13.3英寸笔记本电脑（2017款Core i5处理器/8GB内存/256GB硬盘 MupxT2CH/A）',
                        originalPrice: 12999,
                        favourPrice: 10688,
                        tip: '秒杀'
                    },
                    {
                        image: '../../static/img/goods/p4.jpg',
                        title: 'Kindle Paperwhite电纸书阅读器 电子书墨水屏 6英寸wifi 黑色',
                        originalPrice: 999,
                        favourPrice: 958,
                        tip: '秒杀'
                    },
                    {
                        image: '../../static/img/goods/p5.jpg',
                        title: '微软（Microsoft）新Surface Pro 二合一平板电脑笔记本 12.3英寸（i5 8G内存 256G存储）',
                        originalPrice: 8888,
                        favourPrice: 8288,
                        tip: '优惠'
                    }
                ];

                if (action === 'refresh') {
                    this.productList = [];
                }

                data.forEach(item => {
                    this.productList.push(item);
                });
            },
			showmode(){
				this.$refs.popup.open()
			},
			hiddenmode(){
				this.$refs.popup.close()
			},
			//获取柜机商品
			async getallgood(sn){
				let info= await this.$apis.getallgood({sn:sn});
				console.log(info.data,'返回商品')
				this.myproductList=info.data;
			},
			//用户所拿商品
			async getgood(orderId){
				let info= await this.$apis.getgood({orderId:orderId});
				console.log(info.data,'返回用户商品')
				if(info.data.data.products.length>0){
					this.showmode()
				}
				if(info.data.data.close){
					this.hiddenmode()
					uni.showToast({
						title:'已关门'
					})
					clearInterval(this.timerId);
				}
				this.userproductList=info.data.data.products;
				this.totalmoney(info.data.data.products)
			},
			toIndex() {
				uni.reLaunch({
					url:'../../pages/index/index'
				})
			},
			//计算价格
			totalmoney(data){
				var selectArr = data;   //已选择的商品
				    var totalPrice = 0;
				    if(selectArr.length){  //如果存在商品就计算价格
				      for(var i=0;i<selectArr.length;i++){
				        totalPrice += selectArr[i].number * selectArr[i].price;
				      }
				      totalPrice = totalPrice.toFixed(2);  //乘法有点问题, 需要保留一下小数
				      console.log("计算价格:",totalPrice)
				      this.allmoney=totalPrice;
				    } else{  //不存在商品就把总价格置为 0
				      this.allmoney=0;
				    }
			}
        },
        onLoad(option) {
			console.log(option.productNumber,'传过来')
			this.$refs.popups.open()
			this.mycouponlist()
			this.getallgood(option.productNumber)
            //this.loadData();
            setTimeout(() => {
                this.renderImage = true;
            }, 300);
			let orderId=uni.getStorageSync('ordernum');
			// this.timerId = setInterval(() => {
			// 		var reqTime = this.reqTime;
			// 		reqTime--;
			// 		this.reqTime = reqTime;
			// 		if (reqTime < 1) {
			// 			clearInterval(this.timerId);
			// 			uni.reLaunch({
